<template>
  <div class="organize_report">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>

    <div class="report_table">
      <div class="report_button">
        <Button type="primary" @click="handleSubmit(true)">批量组庭呈报</Button>
      </div>
      <Table
      @on-select="selectOne"
      @on-select-cancel="selectCancel"
      @on-select-all="selectAll"
      @on-select-all-cancel="selectAllCancel"
      :columns="columns" 
      :data="tableData">
        <template slot-scope="{ row }" slot="state">
         <a href="javascript:;">{{caseStatus[row['state']]}}</a>
        </template>
        <template slot-scope="{ row}" slot="action">
          <a href="javascript:;" style="margin-right:20px" @click="handleSubmit(false,row)" title="组庭呈报"><img width="26" height="26" src="../../assets/images/zhuting1.png" alt=""></a>
          <a href="javascript:;" @click="donload(row)" title="查看文件"><img width="26" height="26" src="../../assets/images/zhuting2.png" alt=""></a>
        </template>
      </Table>
    </div>
    <!-- 分页 -->
    <Page style="margin-top:20px" 
      :current="currentPage" 
       :total="total"
       :page-size="pageSize"
       :page-size-opts="pageOpts" 
       @on-change="changeCurrentPage"
       @on-page-size-change="changeSize"
       show-sizer show-elevator show-total />
   

    <!-- 文件列表 -->
    <Modal v-model="fileDialog" title="文件列表" :footer-hide="true">
      <div class="file_dialog">
        <Table class="file_table" :columns="fileColumns" :data="fileTable">
          <template slot-scope="{ row, index }" slot="action">
            <div class="file_btn">
              <button @click="look(row)">预览</button>
              <button @click="donloadFile">下载</button>
            </div>
          </template>
        </Table>
        <div class="file_footer">
          <Page :current="1" :total="10" :page-size="3" />
          <div class="close">
            <a href="#">关闭</a>
            <a href="#">一键下载</a>
          </div>
        </div>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue"
import {listMixin} from './mixin.js'
import { mapActions } from 'vuex'
const columns = [
        {
          type: "selection",
          width: 100,
          align: "center"
        },
        {
          title: "案号",
          key: "code",
          align: "center"
        },
        {
          title: "案件类型",
          key: 'caseTypeName',
          align: "center"
        },
        {
          title: "申请人",
          key: 'applicantName',
          align: "center"
        },
        {
          title: "申请人仲裁员",
          key: "applicantArbName",
          align: "center"
        },
        {
          title: "被申请人",
           key: "respondentName",
          align: "center"
        },
        {
          title: "被申请人仲裁员",
          key: "respondentArbName",
          align: "center"
        },
        {
          title: "确认仲裁员",
          key: "finalArbName"
        },
        {
          title: "案件状态",
          key: "state",
          slot:"state"
        },
        {
          title: "操作",
          key: "action",
          slot: "action",
          width: 150,
          align: "center"
        }
      ]
export default {
  components: {
    listSearch
  },
  mixins:[listMixin],
  data() {
    return {
      searchData: {
        number: "",
        person: "",
        backPerson: "",
        name: [],
        type: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "person",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backPerson",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "name",
          label: "注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        },
        {
          type: "select",
          value: "type",
          label: "案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "合同纠纷", value: 1 },
            { label: "小额贷款", value: 2 },
            { label: "借贷纠纷", value: 3 }
          ],
          placeholder: "请选择"
        }
      ],
      columns: columns,
      fileDialog: false,
      fileColumns: [
        {
          title: "角色",
          key: "type"
        },
        {
          title: "文件名",
          key: "pdf"
        },
        {
          title: "操作",
          slot: "action",
          width: 150,
          align: "center"
        }
      ],
      fileTable: [
        {
          type: "11",
          pdf: "pdf"
        },
        {
          type: "11",
          pdf: "pdf"
        }
      ]
    };
  },
  mounted(){
  },
  methods: {
    ...mapActions({
      getRequsetList:"createCase/getRequsetList",
      setReport:"organizePeriod/setReport"
    }),
    // value true 批量操作 false 单个操作
    async handleSubmit(value,row) {
      let length = this.selectedData.length;
       if (value &&length === 0) {
          this.$Message.warning("请至少选择一条案件信息");
          return;
      }

      this.$Modal.confirm({
          title: "操作",
          content:
            `<div style="color:#3B4453;text-align:center;font-size:18px">您确定要${value?'批量通过'+ length+'条':'提交'}组庭呈报吗?</div>`,
          onOk: async() => {
            // 批量
            // 单个
          
            let params = {
                caseIds:'',//案件id
                actionIdentifer:"GROUP_SUBMIT", //操作标识
            }
            if(value){
              params.caseIds = JSON.stringify(this.selectedData)
            } else {
              params.caseIds=JSON.stringify(new Array(row.id))
            }
            console.log(params, '参数')
            const result =await this.setReport(params)
            if (result.data.flag) {
                  this.$Message.success("操作成功");
                  this.getList()
                } else {
                  this.$Message.error(result.data.message);
                }
           
            
          },
          onCancel: () => {
            console.log('取消了', this.tableData)
          }
        });
    },
    
    async getList(){
      
      let params = {
        menuIdentifer: "GROUP_PRE_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter ={//筛选条件
        applicantName:"",//申请人关键字
        respondentName:"",//被申请人关键字
        userId:'' ,//注册名称的用户id
        caseTypeId: '',//案件类型id
    	  createTimeBegin:"",//提交的开始时间
        createTimeEnd:"" //提交的结束时间
    }

      params = { ...params,filter:JSON.stringify({}) };
      // console.log("参数", params);
      const res= await this.getRequsetList(params)
      if (res.data.flag) {
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
         this.selectedData=[] // 重置
      }
    },
    donload(row) {

      this.fileDialog = true;
    },
    goSearch() {
      console.log("-goSearch-");
    },
    look() {

    },
    donloadFile() {},
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}
/deep/.file_dialog {
  .ivu-page-next,
  .ivu-page-prev {
    display: none !important;
  }
}
.organize_report {
  .report_button {
    text-align: right;
    padding-right: 20px;
    background-color: #fff;
    height: 80px;
    line-height: 80px;
  }
  .report_table {
    margin-top: 20px;
  }
  .mgt20 {
    margin-top: 20px;
  }
}

.file_btn {
  display: flex;
  justify-content: space-between;
  button {
    color: #3296fa;
    border: none;
    background-color: #fff;
  }
  button:hover {
    background-color: rgba(235, 247, 255);
  }
}
.file_table {
  margin-bottom: 20px;
}
.file_dialog {
  .file_footer {
    position: relative;
  }
  .close {
    position: absolute;
    top: 10px;
    right: 0;
    a:first-child {
      margin-right: 20px;
      color: #000;
    }
  }
}
</style>


